import React, { useState, useEffect } from "react";
import "../css/ScheduleRegistration.css";

function ScheduleRegistration({ onClose }) {
    const [matchDate, setMatchDate] = useState("");
    const [matchTime, setMatchTime] = useState("");
    const [venue, setVenue] = useState("");
    const [opponent, setOpponent] = useState("");
    const [schedules, setSchedules] = useState([]);  //   

    const venueOptions = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "iMPARK",
        "",
        ""
    ];

    const opponentOptions = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "fc"
    ];

    // 30    
    const generateTimeOptions = () => {
        const times = [];
        for (let hour = 0; hour < 24; hour++) {
            for (let minute = 0; minute < 60; minute += 30) {
                const formattedHour = hour < 10 ? `0${hour}` : hour;
                const formattedMinute = minute < 10 ? `0${minute}` : minute;
                times.push(`${formattedHour}:${formattedMinute}`);
            }
        }
        return times;
    };

    //   : "YYYY-MM-DD" -> "MD"
    const formatDate = (dateString) => {
        const parts = dateString.split("-");
        if (parts.length < 3) return dateString;
        const month = parseInt(parts[1], 10);
        const day = parseInt(parts[2], 10);
        return `${month}${day}`;
    };

    //   : "HH:mm:ss"  "HH:mm" -> "HH:mm"
    const formatTime = (timeString) => {
        const parts = timeString.split(":");
        if (parts.length < 2) return timeString;
        return `${parts[0]}:${parts[1]}`;
    };

    //     
    const handleSubmit = (e) => {
        e.preventDefault();

        let homeTeam, awayTeam;
        if (venue === "") {
            homeTeam = "";
            awayTeam = opponent;
        } else {
            homeTeam = opponent;
            awayTeam = "";
        }

        const schedule = {
            matchDate: matchDate,
            matchTime: matchTime,
            homeTeam: homeTeam,
            awayTeam: awayTeam,
            location: venue,
        };

        fetch("/api/schedule", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            credentials: "include",
            body: JSON.stringify(schedule),
        })
            .then((response) => {
                if (!response.ok) {
                    throw new Error("  .");
                }
                return response.json();
            })
            .then((data) => {
                console.log("  :", data);
                fetchSchedules();
                if (onClose) {
                    onClose();
                }
            })
            .catch((error) => console.error("  :", error));
    };

    //     
    const fetchSchedules = () => {
        fetch("/api/schedule", {
            credentials: "include",
        })
            .then((response) => {
                if (!response.ok) {
                    throw new Error("   .");
                }
                return response.json();
            })
            .then((data) => {
                setSchedules(data);
            })
            .catch((error) => console.error("   :", error));
    };

    // DELETE     
    const handleDelete = (scheduleId) => {
        fetch(`/api/schedule/${scheduleId}`, {
            method: "DELETE",
            credentials: "include",
        })
            .then((response) => {
                if (!response.ok) {
                    throw new Error("  .");
                }
                //    .
                fetchSchedules();
            })
            .catch((error) => console.error("  :", error));
    };

    //      
    useEffect(() => {
        fetchSchedules();
    }, []);

    return (
        <div className="ScheduleRegistration_page">
            <form onSubmit={handleSubmit}>
                <div className="formField">
                    <label htmlFor="matchDate"> :</label>
                    <input
                        type="date"
                        id="matchDate"
                        value={matchDate}
                        onChange={(e) => setMatchDate(e.target.value)}
                        required
                    />
                </div>
                <div className="formField">
                    <label htmlFor="matchTime"> :</label>
                    <select
                        id="matchTime"
                        value={matchTime}
                        onChange={(e) => setMatchTime(e.target.value)}
                        required
                    >
                        <option value=""></option>
                        {generateTimeOptions().map((time) => (
                            <option key={time} value={time}>{time}</option>
                        ))}
                    </select>
                </div>
                <div className="formField">
                    <label htmlFor="venue">:</label>
                    <select
                        id="venue"
                        value={venue}
                        onChange={(e) => setVenue(e.target.value)}
                        required
                    >
                        <option value=""></option>
                        {venueOptions.map((v) => (
                            <option key={v} value={v}>{v}</option>
                        ))}
                    </select>
                </div>
                <div className="formField">
                    <label htmlFor="opponent">:</label>
                    <select
                        id="opponent"
                        value={opponent}
                        onChange={(e) => setOpponent(e.target.value)}
                        required
                    >
                        <option value=""></option>
                        {opponentOptions.map((team) => (
                            <option key={team} value={team}>{team}</option>
                        ))}
                    </select>
                </div>
                <button type="submit"></button>
            </form>
            {/*      */}
            <div style={{width:"100%"}}>
                <span style={{color:"#EFE7E7" ,marginBottom:"1vh", fontSize:"15px", fontWeight:"bold"}}>
                      
                </span>
            </div>
            <div className="ScheduleRegistration">
                {schedules.length === 0 ? (
                    <p>  .</p>
                ) : (
                    <ul>
                        {schedules.map((sch) => (
                            <li key={sch.scheduleId}>
                                {formatDate(sch.matchDate)} {formatTime(sch.matchTime)} | {sch.homeTeam} vs. {sch.awayTeam} | : {sch.location}
                                <button
                                    className="deleteButton"
                                    onClick={() => handleDelete(sch.scheduleId)}
                                >
                                    X
                                </button>
                            </li>
                        ))}
                    </ul>
                )}
            </div>
        </div>
    );
}

export default ScheduleRegistration;
