import React from "react";
import { Doughnut } from "react-chartjs-2";
import "chart.js/auto"; //  Chart.js   
import ChartDataLabels from "chartjs-plugin-datalabels"; //  import
import "../css/AttendanceDoughnutChart.css";  // CSS  import

//  :   0 ,   1 
const formatPercentage = (value) => {
    return value % 1 === 0 ? value.toString() : value.toFixed(1);
};

function ScheduleWinRateChart({ winCount, drawCount, loseCount }) {
    //    
    const total = winCount + drawCount + loseCount;
    //   (   0 "0" ,    )
    const computedWinRate = total > 0 ? (winCount / total) * 100 : 0;
    const winRate = formatPercentage(computedWinRate);

    const data = {
        labels: ["", "", ""],
        datasets: [
            {
                data: [winCount, drawCount, loseCount],
                backgroundColor: ["#36A2EB", "#FFCE56", "#FF6384"],
            },
        ],
    };

    const options = {
        maintainAspectRatio: false,
        cutout: "60%", //    
        plugins: {
            legend: {
                display: false, //  
            },
            datalabels: {
                //      
                formatter: (value) => {
                    let perc = total > 0 ? (value / total) * 100 : 0;
                    return formatPercentage(perc) + "%";
                },
                color: "#fff", //  
                font: {
                    size: 10,
                    weight: "bold",
                },
                anchor: "center",
                align: "center",
            },
        },
        animation: {
            duration: 1500,
            easing: "easeInOutQuad",
        },
    };

    return (
        <div style={{ height: "50vh" }}>
            <div className="Chart_title">
                <span>2025 FC</span>
            </div>
            <div className="attendance-doughnut-container">
                <Doughnut data={data} options={options} plugins={[ChartDataLabels]} />
            </div>
            <div>
                <div className="Schedule_Statistics_btn_container">
                    <div className="Schedule_Statistics_btn"> : {total}</div>
                </div>
            </div>
            <div>
                <div className="Schedule_Statistics_btn_container">
                    <div className="Schedule_Statistics_btn">
                        {winCount} {drawCount} {loseCount}
                    </div>
                </div>
            </div>
            <div>
                <div className="Schedule_Statistics_btn_container">
                    <div className="Schedule_Statistics_btn"> : {winRate}%</div>
                </div>
            </div>
        </div>
    );
}

export default ScheduleWinRateChart;
