// src/components/ProfileModal.js
import React, { useState, useEffect } from "react";
import "../css/ProfileModal.css";

function ProfileModal({ user, onClose }) {
    const [nickname, setNickname] = useState("");
    const [error, setError] = useState("");

    // (A-Z, a-z)    
    const validRegex = /^[A-Za-z---]*$/;

    //  1  0.5,  1  1   
    const calcWeightedLength = (str) => {
        const enCount = (str.match(/[A-Za-z]/g) || []).length;
        const korCount = (str.match(/[---]/g) || []).length;
        return enCount * 0.5 + korCount;
    };

    //  
    useEffect(() => {
        if (user) {
            setNickname(user.nickname);
            setError("");
        }
    }, [user]);

    const handleChange = (e) => {
        const value = e.target.value;
        //  
        if (!validRegex.test(value)) {
            setError("    .");
            return;
        }
        //  
        const len = calcWeightedLength(value);
        if (len > 6) {
            setError(" 6( 0.5,  1)  .");
            return;
        }
        setNickname(value);
        setError("");
    };

    const handleSave = () => {
        if (!nickname.trim()) {
            setError(" .");
            return;
        }
        fetch("/api/profile", {
            method: "PUT",
            headers: { "Content-Type": "application/json" },
            credentials: "include",
            body: JSON.stringify({ nickname }),
        })
            .then(async (res) => {
                const text = await res.text();
                if (!res.ok) {
                    throw new Error(text || "  .");
                }
                return JSON.parse(text);
            })
            .then((updatedUser) => {
                onClose(updatedUser);
                alert(" .");
            })
            .catch((err) => {
                setError(err.message);
            });
    };

    const handleLogout = () => {
        fetch("/logout", {
            method: "POST",
            credentials: "include",
            headers: { "Content-Type": "application/x-www-form-urlencoded" },
        }).finally(() => {
            window.location.href = "/";
        });
    };

    return (
        <div className="profile-modal-overlay" onClick={() => onClose()}>
            <div
                className="profile-modal-content"
                onClick={(e) => e.stopPropagation()}
            >
                <button className="modal-close" onClick={() => onClose()}>X</button>
                <h2> </h2>

                <label></label>
                <input type="text" value={nickname} onChange={handleChange} />
                {error && <div className="error-text" style={{ marginTop: "10px",color:"red" }}>{error}</div>}
                <div className="error-text" style={{marginTop:"10px"}}>
                     6( 0.5,  1)  .
                </div>
                <div className="error-text">
                          .
                </div>

                <div className="profile-modal-actions">
                    <button onClick={handleSave} disabled={!!error}></button>
                    <button onClick={() => onClose()}></button>
                </div>

                <div className="logout-section">
                    <button className="logout-button" onClick={handleLogout}>
                        
                    </button>
                </div>
            </div>
        </div>
    );
}

export default ProfileModal;
