// src/components/messenger/ChatList.js

import React, { useEffect, useState } from 'react';
import '../../css/Messenger.css';

/**
 * ChatList.js
 * Props:
 *  - activeChat: string | null,    ID
 *  - onSelectChat: (chatId: string) => void,   
 *  - user: object,     ({ userId, nickname, ... })
 */
export default function ChatList({ activeChat, onSelectChat, user }) {
    const [rooms, setRooms] = useState([]);            //   ({ userId, nickname }[])
    const [incoming, setIncoming] = useState([]);     //   ({ userId, nickname }[])
    const [outgoing, setOutgoing] = useState([]);     //   ({ userId, nickname }[])
    const [newNickname, setNewNickname] = useState('');
    const [error, setError] = useState(null);
    const [selectedTab, setSelectedTab] = useState('friends'); //  
    const [menuOpenId, setMenuOpenId] = useState(null);


    //    (DTO: { userId, nickname })
    const fetchRooms = () => {
        fetch('/api/friends', { credentials: 'include' })
            .then(res => res.json())
            .then(data => {
                setRooms(Array.isArray(data) ? data : []);
                setError(null);
            })
            .catch(() => setError('   '));
    };

    //    
    const fetchIncoming = () => {
        fetch('/api/friends/requests', { credentials: 'include' })
            .then(res => res.json())
            .then(data => setIncoming(Array.isArray(data) ? data : []))
            .catch(err => console.error('Incoming fetch error:', err));
    };

    //    
    const fetchOutgoing = () => {
        fetch('/api/friends/requests/sent', { credentials: 'include' })
            .then(res => res.json())
            .then(data => setOutgoing(Array.isArray(data) ? data : []))
            .catch(err => console.error('Outgoing fetch error:', err));
    };

    useEffect(() => {
        fetchRooms();
        fetchIncoming();
        fetchOutgoing();
    }, []);

    //    ( )
    const handleAddFriend = () => {
        if (!newNickname.trim()) return;
        fetch('/api/friends', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            credentials: 'include',
            body: JSON.stringify({ nickname: newNickname.trim() })
        })
            .then(res => {
                if (!res.ok) throw new Error('  ');
                setNewNickname('');
                fetchRooms();
                fetchIncoming();
                fetchOutgoing();
            })
            .catch(err => setError(err.message));
    };

    //   
    const handleAccept = userObj => {
        fetch(`/api/friends/${userObj.userId}/accept`, {
            method: 'PUT', credentials: 'include'
        })
            .then(res => {
                if (!res.ok) throw new Error(' ');
                fetchRooms();
                fetchIncoming();
                fetchOutgoing();
            })
            .catch(err => setError(err.message));
    };

    //   
    const handleReject = userObj => {
        fetch(`/api/friends/${userObj.userId}/reject`, {
            method: 'PUT', credentials: 'include'
        })
            .then(res => {
                if (!res.ok) throw new Error(' ');
                fetchIncoming();
                fetchOutgoing();
            })
            .catch(err => setError(err.message));
    };

    //   
    const handleCancel = userObj => {
        fetch(`/api/friends/${userObj.userId}`, {
            method: 'DELETE', credentials: 'include'
        })
            .then(res => {
                if (!res.ok) throw new Error(' ');
                fetchRooms();
                fetchIncoming();
                fetchOutgoing();
            })
            .catch(err => setError(err.message));
    };

    // 1)   
    const handleRemoveFriend = (friendId) => {
        fetch(`/api/friends/${friendId}`, {
            method: 'DELETE',
            credentials: 'include'
        })
            .then(res => {
                if (res.status === 204) {
                    //  :    
                    fetchRooms();
                } else {
                    throw new Error(' ');
                }
            })
            .catch(err => {
                console.error('Remove friend error:', err);
            });
    };

    return (
        <div className="chat-list-tabs">
            {/* 1.   */}
            <nav className="tab-header">
                <button
                    className={selectedTab === 'friends' ? 'active' : ''}
                    onClick={() => setSelectedTab('friends')}
                >
                    
                </button>
                <button
                    className={selectedTab === 'outgoing' ? 'active' : ''}
                    onClick={() => setSelectedTab('outgoing')}
                >
                    
                </button>
                <button
                    className={selectedTab === 'incoming' ? 'active' : ''}
                    onClick={() => setSelectedTab('incoming')}
                >
                    
                </button>
                <button
                    className={selectedTab === 'add' ? 'active' : ''}
                    onClick={() => setSelectedTab('add')}
                >
                    
                </button>
            </nav>

            {/* 2.   */}
            <div className="tab-content">
                {selectedTab === 'friends' && (
                    <ul className="chat-list">
                        {rooms.map(u => (
                            <li
                                key={u.userId}
                                className={`chat-list-item ${menuOpenId === u.userId ? 'open' : ''}`}
                                onClick={() => setMenuOpenId(menuOpenId === u.userId ? null : u.userId)}
                            >
                                <span className="chat-list-name">{u.nickname}</span>

                                {/*      */}
                                {menuOpenId === u.userId && (
                                    <div className="chat-list-dropdown">
                                        <ul>
                                            <li
                                                className="chat-list-dropdown-item"
                                                onClick={() => {
                                                    onSelectChat(u.userId);
                                                    setMenuOpenId(null); //  
                                                }}
                                            >
                                                1:1 
                                            </li>
                                            <li onClick={() => handleRemoveFriend(u.userId)}> </li>
                                        </ul>
                                    </div>
                                )}
                            </li>
                        ))}
                    </ul>
                )}

                {selectedTab === 'outgoing' && (
                    <ul>
                        {outgoing.length > 0
                            ? outgoing.map(u => (
                                <li key={u.userId}>
                                    {u.nickname}
                                    <button className="chat-list-button-false" onClick={() => handleCancel(u)}></button>
                                </li>
                            ))
                            : <p></p>
                        }
                    </ul>
                )}

                {selectedTab === 'incoming' && (
                    <ul>
                        {incoming.length > 0
                            ? incoming.map(u => (
                                <li key={u.userId}>
                                    {u.nickname}
                                    <button className="chat-list-button-true" onClick={() => handleAccept(u)}></button>
                                    <button className="chat-list-button-false" onClick={() => handleReject(u)}></button>
                                </li>
                            ))
                            : <p></p>
                        }
                    </ul>
                )}

                {selectedTab === 'add' && (
                    <div className="add-friend-section-div">
                        <input
                            className="add-friend-section"
                            type="text"
                            placeholder="  "
                            value={newNickname}
                            onChange={e => setNewNickname(e.target.value)}
                        />
                        <div>
                            <button onClick={handleAddFriend}></button>
                            {error && <div className="error-text">{error}</div>}
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}
