// src/components/messenger/MessengerModal.js

import React, { useEffect, useRef, useState } from 'react';
import ReactDOM from 'react-dom';
import ChatList from './ChatList';
import ChatWindow from './ChatWindow';
import '../../css/Messenger.css';

/**
 * MessengerModal.js
 * Props:
 *  - isOpen: boolean,   
 *  - onClose: () => void,   
 *  - user: object,     ({ userId, nickname, ... })
 */
export default function MessengerModal({ isOpen, onClose, user }) {
    const [activeChat, setActiveChat] = useState(null);
    const [animating, setAnimating] = useState(false);
    const modalRef = useRef();

    //     
    useEffect(() => {
        if (activeChat) {
            requestAnimationFrame(() => setAnimating(true));
        } else {
            setAnimating(false);
        }
    }, [activeChat]);

    // ESC  
    useEffect(() => {
        function handleKey(e) {
            if (e.key === 'Escape') onClose();
        }
        if (isOpen) window.addEventListener('keydown', handleKey);
        return () => window.removeEventListener('keydown', handleKey);
    }, [isOpen, onClose]);

    //    
    function handleBackdropClick(e) {
        if (modalRef.current && !modalRef.current.contains(e.target)) {
            onClose();
        }
    }

    if (!isOpen) return null;

    return ReactDOM.createPortal(
        <div
            className="messenger-modal-overlay"
            onMouseDown={handleBackdropClick}
        >
            <div ref={modalRef} className="messenger-modal-content">
                <button className="modalCloseBtn" onClick={onClose}>X</button>

                {/* : activeChat  ,   */}
                <aside
                    className={
                        activeChat
                            ? 'messenger-sidebar'
                            : 'messenger-sidebar-collapsed'
                    }
                >
                    <ChatList
                        activeChat={activeChat}
                        onSelectChat={setActiveChat}
                        user={user}
                    />
                </aside>

                {/*  : book-open  */}
                <section className={`messenger-main ${animating ? 'open' : ''}`}>
                    {activeChat ? (
                        <ChatWindow
                            userId={user.userId}     //   ID
                            friendId={activeChat}
                            //   ID
                        />
                    ) : (
                        <div className="no-chat-selected"> .</div>
                    )}
                </section>
            </div>
        </div>,
        document.body
    );
}
